import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const IndexIndex = r => require.ensure([],()=>r(require('../pages/index/index.vue')),'IndexIndex')
const FirstFirst = r => require.ensure([],()=>r(require('../pages/index/children/first.vue')),'FirstFirst')
const OneOne = r => require.ensure([],()=>r(require('../pages/index/children/one.vue')),'OneOne')
const LastLast = r => require.ensure([],()=>r(require('../pages/index/children/children/last.vue')),'LastLast')


export default new Router({
    routes: [
        {
            path: '/',
            component: IndexIndex
        },
        {
            path:'/index',
            component:IndexIndex,
            children:[
                {
                    path:'first',
                    component:FirstFirst,
                    children:[
                        {
                            path:'last',
                            component:LastLast
                        }
                    ]
                },{
                    path:'one',
                    component:OneOne,
                }
            ]
        }
    ]
})



