import axios from 'axios'
import Qs from 'qs'

axios.defaults.baseURL = 'https://www.easy-mock.com/mock/5abca92a69ca000c0358d052'

export default  async(url,data={},methods='post')=>{
    if(methods == 'post'){
        return axios.post(url,data,{
            // 这里可以在发送请求之前对请求数据做处理，比如form-data格式化等，这里可以使用开头引入的Qs（这个模块在安装axios的时候就已经安装了，不需要另外安装）
            transformRequest:[
                (data)=>{
                    data.token = 11111
                    return Qs.stringify(data)
                }
            ],
            transformResponse:[
                (data) =>{
                    return data
                }
            ]
        })
            .then(response=> response.data)
            .then((json)=>{
                if(json.code == "200"){
                    return json.data
                }
            })
    }
}


